import { atom } from "recoil";
import {
  FestivalData,
  FestivalDetailData,
  RestaurantData,
  RestaurantDetailData,
} from "../types/around";

export const selectedAroundRestaurantState = atom<
  RestaurantData | RestaurantDetailData | null
>({
  key: "selectedAroundRestaurantState",
  default: null,
});

export const selectedAroundFestivalState = atom<
  FestivalData | FestivalDetailData | null
>({
  key: "selectedAroundFestivalState",
  default: null,
});

export const aroundRestaurantsState = atom<RestaurantData[] | null>({
  key: "aroundRestaurantsState",
  default: [],
});

export const aroundRestaurantDetailState = atom<RestaurantDetailData | null>({
  key: "aroundRestaurantDetailState",
  default: null,
});

export const aroundFestivalsState = atom<FestivalData[] | null>({
  key: "aroundFestivalsState",
  default: [],
});

export const aroundFestivalDetailState = atom<FestivalDetailData | null>({
  key: "aroundFestivalDetailState",
  default: null,
});

export const aroundFilterCategoryState = atom({
  key: "aroundFilterCategoryState",
  default: "restaurant",
});

export const selectedRestaurantCoordState = atom<{
  lat: number;
  lng: number;
} | null>({
  key: "selectedRestaurantCoordState",
  default: null,
});

export const selectedFestivalCoordState = atom<{
  lat: number;
  lng: number;
} | null>({
  key: "selectedFestivalCoordState",
  default: null,
});

export const currentCoordState = atom<{
  lat: number;
  lng: number;
} | null>({
  key: "currentCoordState",
  default: null,
});
