import React, { useEffect, useState } from "react";
import { useRecoilValue, useSetRecoilState } from "recoil";
import {
  selectedAroundRestaurantState,
  selectedRestaurantCoordState,
} from "@states/aroundAtoms";
import { fetchRestaurantDetail } from "@services/aroundService";
import { RestaurantDetailData } from "../../../types/around";
import styled from "styled-components";
import Icon from "@components/Icon";
import { ICONS } from "@constants/icons";

const RestaurantDetail: React.FC = () => {
  const selectedRestaurant = useRecoilValue(selectedAroundRestaurantState);
  const setRestaurantCoord = useSetRecoilState(selectedRestaurantCoordState);
  const [restaurantDetail, setRestaurantDetail] =
    useState<RestaurantDetailData | null>(null);

  useEffect(() => {
    if (selectedRestaurant && "restaurantId" in selectedRestaurant) {
      fetchRestaurantDetail(selectedRestaurant.restaurantId)
        .then((data) => {
          setRestaurantDetail(data);
        })
        .catch((error) => {
          console.error("Failed to fetch restaurant details:", error);
        });
    }
  }, [selectedRestaurant]);

  useEffect(() => {
    console.log("Restaurant Detail State:", restaurantDetail);
  }, [restaurantDetail]);

  if (!restaurantDetail) {
    return <div>Loading...</div>;
  }

  const coord = {
    lat: restaurantDetail.latitude,
    lng: restaurantDetail.longitude,
  };
  setRestaurantCoord(coord);

  return (
    <Wrapper>
      <ImageWrapper>
        {restaurantDetail.imageUrl ? (
          <Image src={restaurantDetail.imageUrl} />
        ) : (
          <NoImage>
            <Icon name={ICONS.NO_IMAGE} />
          </NoImage>
        )}
      </ImageWrapper>
      <TitleWrapper>
        <Category>식당</Category>
        <Title>{restaurantDetail.restaurantName}</Title>
      </TitleWrapper>
      <ContentWrapper>
        <BoxWrapper>
          <Type>운영 정보</Type>
          <Box>
            {restaurantDetail.address && (
              <Info>
                <Icon
                  name={ICONS.ADDRESS}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <p> {restaurantDetail.address}</p>
              </Info>
            )}

            {restaurantDetail.operatingHours && (
              <Info>
                <Icon
                  name={ICONS.TIME}
                  size={14}
                  color="white"
                  hoverColor="white"
                />

                <p> {restaurantDetail.operatingHours}</p>
              </Info>
            )}

            {restaurantDetail.contact && (
              <Info>
                <Icon
                  name={ICONS.PHONE}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <p> {restaurantDetail.contact}</p>
              </Info>
            )}

            {restaurantDetail.homepage && (
              <Info>
                <Icon
                  name={ICONS.WEBSITE}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <a href={restaurantDetail.homepage} target="__blank">
                  <p>{restaurantDetail.homepage}</p>
                </a>
              </Info>
            )}
          </Box>
        </BoxWrapper>
        <BoxWrapper>
          <Type>설명</Type>
          <Box>{restaurantDetail.details}</Box>
        </BoxWrapper>
      </ContentWrapper>
    </Wrapper>
  );
};

export default RestaurantDetail;

const Wrapper = styled.div`
  background-color: #fcfcfc;
  height: 98vh;
  box-shadow: ${(props) => props.theme.boxShadow};
  border-radius: 10px;
`;

const ImageWrapper = styled.div`
  padding-top: 13px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: ${(props) => props.theme.colors.background};

  border-radius: 10px 10px 0 0;
`;

const Image = styled.img`
  width: 360px;
  height: 230px;
  border-radius: 6px;
  box-shadow: ${(props) => props.theme.boxShadow};
`;

const NoImage = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
  color: ${(props) => props.theme.colors.neutral.mediumGrey};

  width: 360px;
  height: 230px;
  border-radius: 6px;
  box-shadow: ${(props) => props.theme.boxShadow};
  background-color: ${(props) => props.theme.colors.neutral.lightGrey};
`;

const TitleWrapper = styled.div`
  padding-left: 12px;
  background-color: ${(props) => props.theme.colors.background};
  padding-bottom: 20px;
  border-bottom: 2px solid ${(props) => props.theme.colors.neutral.lightGrey};
`;
const Title = styled.div`
  width: 350px;
  font-size: 20px;
  color: ${(props) => props.theme.colors.primary};
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
`;
const Category = styled.div`
  padding-top: 10px;
  padding-bottom: 3px;
  font-size: 12px;
  color: ${(props) => props.theme.colors.semantic.success};
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
`;

const ContentWrapper = styled.div`
  overflow-y: scroll;
  padding-bottom: 20px;
  border-radius: 0 0 10px 10px;
  width: 100%;
  min-height: 100%;
`;

const BoxWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Box = styled.div`
  line-height: 16px;
  width: 350px;
  padding: 15px;
  background-color: ${(props) => props.theme.colors.background};
  box-shadow: ${(props) => props.theme.boxShadow};
  font-family: ${(props) => props.theme.fonts.pretendard.regular};
  font-size: 13px;
  overflow-y: scroll;
  max-height: 165px;
`;

const Type = styled.div`
  font-family: ${(props) => props.theme.fonts.pretendard.bold};
  font-size: 13px;
  margin-left: 15px;
  color: #4e4e4e;
  display: flex;
  margin-top: 20px;
  margin-bottom: 5px;
  align-self: flex-start;
`;

