import React, { useEffect, useState } from "react";
import { useRecoilValue, useSetRecoilState } from "recoil";
import {
  selectedAroundFestivalState,
  selectedFestivalCoordState,
} from "@states/aroundAtoms";
import { fetchFestivalDetail } from "@services/aroundService";
import { FestivalDetailData } from "../../../types/around";
import styled from "styled-components";
import Icon from "@components/Icon";
import { ICONS } from "@constants/icons";

const FestivalDetail: React.FC = () => {
  const selectedFestival = useRecoilValue(selectedAroundFestivalState);
  const setFestivalCoord = useSetRecoilState(selectedFestivalCoordState);

  const [festivalDetail, setfestivalDetail] =
    useState<FestivalDetailData | null>(null);

  useEffect(() => {
    if (selectedFestival && "festivityId" in selectedFestival) {
      fetchFestivalDetail(selectedFestival.festivityId)
        .then((data) => {
          setfestivalDetail(data);
        })
        .catch((error) => {
          console.error("Failed to fetch Festival details:", error);
        });
    }
  }, [selectedFestival]);

  useEffect(() => {
    console.log("Festival Detail State:", festivalDetail);
  }, [festivalDetail]);

  if (!festivalDetail) {
    return <div>Loading...</div>;
  }

  const coord = {
    lat: festivalDetail.latitude,
    lng: festivalDetail.longitude,
  };
  setFestivalCoord(coord);

  return (
    <Wrapper>
      <ImageWrapper>
        <Image src={festivalDetail.thumbnailUrl} />
      </ImageWrapper>
      <TitleWrapper>
        <Category>축제/행사</Category>
        <Title>{festivalDetail.title}</Title>
      </TitleWrapper>
      <ContentWrapper>
        <BoxWrapper>
          <Type>운영 정보</Type>
          <Box>
            {festivalDetail.address && (
              <Info>
                <Icon
                  name={ICONS.ADDRESS}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <p> {festivalDetail.address}</p>
              </Info>
            )}

            {festivalDetail.operatingDaysHours && (
              <Info>
                <Icon
                  name={ICONS.TIME}
                  size={14}
                  color="white"
                  hoverColor="white"
                />

                <p> {festivalDetail.operatingDaysHours}</p>
              </Info>
            )}

            {festivalDetail.usageFee && (
              <Info>
                <Icon
                  name={ICONS.COST}
                  size={14}
                  color="white"
                  hoverColor="white"
                />

                <p> {festivalDetail.usageFee}</p>
              </Info>
            )}

            {festivalDetail.contact && (
              <Info>
                <Icon
                  name={ICONS.PHONE}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <p> {festivalDetail.contact}</p>
              </Info>
            )}

            {festivalDetail.website && (
              <Info>
                <Icon
                  name={ICONS.WEBSITE}
                  size={14}
                  color="white"
                  hoverColor="white"
                />
                <a href={festivalDetail.website} target="__blank">
                  <p>{festivalDetail.website}</p>
                </a>
              </Info>
            )}

            {festivalDetail.facilities && (
              <Info>
                <Icon name={ICONS.WHEELCHAIR} size={14} />
                <p> {festivalDetail.facilities}</p>
              </Info>
            )}
          </Box>
        </BoxWrapper>
        <BoxWrapper>
          <Type>설명</Type>
          <Box>{festivalDetail.details}</Box>
        </BoxWrapper>
      </ContentWrapper>
    </Wrapper>
  );
};

export default FestivalDetail;

