import Accordion from "@components/common/Accordion";
import { ROUTES } from "@constants/routes";
import { useLocation } from "react-router-dom";
import styled from "styled-components";
import SettlementSurvey from "./Sattlememt/Survey";
import SettlementResult from "./Sattlememt/Result";
import TravleSurvey from "./Travel/Survey";
import TravleResult from "./Travel/Result";
import MapComponent from "@components/common/Map";
import MyMapComponent from "@components/common/Map/MyMapComponent";
import { useRecoilValue } from "recoil";
import { settlementSurveyResultState } from "@states/sattlementAtoms";
import { travelSurveyResultState } from "@states/travelAtoms";
import ArroundList from "./Around/List";

export const Map = () => {
  const location = useLocation();
  const isLoading = false;
  const isSettlementResult = useRecoilValue(settlementSurveyResultState);
  const isTravleResult = useRecoilValue(travelSurveyResultState);
  return (
    <PageWrapper>
      <ContentWrapper>
        {location.pathname.includes(ROUTES.STAY) && (
          <Accordion
            title={"AI 기반 주거지 추천 서비스"}
            subtitle={"당신이 찾는 조건을 선택하세요."}
          >
            {isSettlementResult ? <SettlementResult /> : <SettlementSurvey />}
          </Accordion>
        )}
        {location.pathname.includes(ROUTES.TEAVEL) && (
          <Accordion
            title={"AI 기반 관광코스 추천 서비스"}
            subtitle={"당신이 원하는 조건을 선택하세요."}
          >
            {isTravleResult ? <TravleResult /> : <TravleSurvey />}
          </Accordion>
        )}
        {location.pathname.includes(ROUTES.AROUND) && (
          <Accordion
            title={"내 주변에 즐거움을 모아봤어요."}
            subtitle={"부산의 즐길 거리들을 확인해보세요."}
          >
            <ArroundList />
          </Accordion>
        )}
        {/* <MapComponent /> */}
        <MyMapComponent />
      </ContentWrapper>
    </PageWrapper>
  );
};

export default Map;

const PageWrapper = styled.div`
  display: flex;
  height: 100vh;
  width: 100vw;
`;

const ContentWrapper = styled.div`
  display: flex;
  overflow: hidden;
`;

const MapContainer = styled.div`
  width: 100%;
  height: 100%;
`;
