import { useEffect } from "react";
import { useRecoilState } from "recoil";
import { useLocation } from "react-router-dom";
import {
  accordionOpenState,
  cardOpenState,
  previousCardState,
} from "@states/accordionAtoms";

export const useAccordion = () => {
  const [isAccordionOpen, setIsAccordionOpen] =
    useRecoilState(accordionOpenState);
  const [isCardOpen, setIsCardOpen] = useRecoilState(cardOpenState);
  const [prevCardState, setPrevCardState] = useRecoilState(previousCardState);
  const location = useLocation();

  useEffect(() => {
    if (isAccordionOpen) {
      setIsCardOpen(prevCardState);
    } else {
      setPrevCardState(isCardOpen);
      setIsCardOpen(false);
    }
  }, [isAccordionOpen]);

  useEffect(() => {
    setIsAccordionOpen(true);

    // 경로가 /:id 형태인지 확인
    const isDetailRoute = /\/[^/]+\/\d+$/.test(location.pathname);
    setIsCardOpen(isDetailRoute);
  }, [location]);

  const toggleAccordion = () => setIsAccordionOpen(!isAccordionOpen);

  const openCard = () => {
    setIsCardOpen(true);
    setPrevCardState(true);
  };

  return {
    isAccordionOpen,
    isCardOpen,
    toggleAccordion,
    openCard,
  };
};
