import Icon from "@components/Icon";
import { ICONS } from "@constants/icons";
import { settlementSurveyResultState } from "@states/sattlementAtoms";
import React from "react";
import { useRecoilState } from "recoil";
import { useForm, Controller } from "react-hook-form";
import styled from "styled-components";
import axios from "axios"; // 서버로 데이터 전송을 위해 axios 사용

const distances = ["근거리", "중거리", "장거리"];
const infra = [
  { key: "subwayStation", name: "지하철역" },
  { key: "publicParking", name: "공용주차장" },
  { key: "traditionalMarket", name: "전통시장" },
  { key: "policeStation", name: "경찰서" },
  { key: "kindergarten", name: "유치원" },
  { key: "elementarySchool", name: "초등학교" },
  { key: "middleSchool", name: "중학교" },
  { key: "highSchool", name: "고등학교" },
  { key: "beach", name: "해수욕장" },
  { key: "park", name: "공원" },
];
const facilities = [
  { key: "studyRoom", name: "독서실" },
  { key: "tutorAcademy", name: "보습" },
  { key: "artAcademy", name: "미술" },
  { key: "foreignLanguageAcademy", name: "외국어" },
  { key: "musicAcademy", name: "음악" },
  { key: "danceAcademy", name: "무용" },
  { key: "slicedRawFish", name: "횟집" },
  { key: "koreanFood", name: "한식" },
  { key: "japaneseFood", name: "일식" },
  { key: "chineseFood", name: "중식" },
  { key: "coffeeShop", name: "카페" },
  { key: "convenienceStore", name: "편의점" },
  { key: "crimePreventionFacility", name: "치안시설" },
];

interface FormData {
  distances: {
    [key: string]: string;
  };
  facilities: string[];
}

export const Survey = () => {
  const [isSettlementResult, setIsSettlementResult] = useRecoilState(
    settlementSurveyResultState
  );

  const { handleSubmit, control, setValue, watch } = useForm<FormData>({
    defaultValues: {
      distances: {},
      facilities: [],
    },
  });

  const watchFacilities = watch("facilities");

  // 서버에 데이터를 전송하는 함수
  const onSubmit = async (data: any) => {
    const requestData = {
      // 거리 선택 데이터를 서버에 맞게 변환
      subwayStation: data.distances["지하철역"],
      publicParking: data.distances["공용주차장"],
      traditionalMarket: data.distances["전통시장"],
      policeStation: data.distances["경찰서"],
      kindergarten: data.distances["유치원"],
      elementarySchool: data.distances["초등학교"],
      middleSchool: data.distances["중학교"],
      highSchool: data.distances["고등학교"],
      beach: data.distances["해수욕장"],
      park: data.distances["공원"],
      // 시설 선택 여부를 boolean 값으로 변환
      studyRoom: watchFacilities.includes("독서실"),
      tutorAcademy: watchFacilities.includes("보습"),
      artAcademy: watchFacilities.includes("미술"),
      foreignLanguageAcademy: watchFacilities.includes("외국어"),
      musicAcademy: watchFacilities.includes("음악"),
      danceAcademy: watchFacilities.includes("무용"),
      slicedRawFish: watchFacilities.includes("횟집"),
      koreanFood: watchFacilities.includes("한식"),
      japaneseFood: watchFacilities.includes("일식"),
      chineseFood: watchFacilities.includes("중식"),
      coffeeShop: watchFacilities.includes("카페"),
      convenienceStore: watchFacilities.includes("편의점"),
      crimePreventionFacility: watchFacilities.includes("치안시설"),
    };

    console.log(requestData);

    // 서버에 POST 요청 전송
    // try {
    //   const response = await axios.post("/api/settlement", requestData);
    //   console.log("Server Response:", response.data);
    //   setIsSettlementResult(true);
    // } catch (error) {
    //   console.error("Error posting data:", error);
    // }
  };

  const handleAmenitySelect = (amenity: string) => {
    const selectedFacilities = watchFacilities.includes(amenity)
      ? watchFacilities.filter((a: string) => a !== amenity)
      : [...watchFacilities, amenity];
    setValue("facilities", selectedFacilities);
  };

  return (
    <Wrapper>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div>
          <Question>
            주거지와 인프라 사이의 <BoldText>거리</BoldText>를 선택해주세요.
          </Question>
          <Table>
            <thead>
              <tr>
                <th></th>
                {distances.map((distance) => (
                  <th key={distance}>{distance}</th>
                ))}
              </tr>
            </thead>
            <tbody>
              {infra.map((facility) => (
                <tr key={facility.key}>
                  <td>{facility.name}</td>
                  {distances.map((distance) => (
                    <td key={`${facility.key}-${distance}`}>
                      <Controller
                        control={control}
                        name={`distances.${facility.name}`}
                        render={({ field: { onChange, value } }) => (
                          <Checkbox
                            type="checkbox"
                            checked={value === distance}
                            onChange={() => onChange(distance)}
                          />
                        )}
                      />
                    </td>
                  ))}
                </tr>
              ))}
            </tbody>
          </Table>
          <Question>
            주거지 주변에 있어야 하는 <BoldText>시설</BoldText>을 선택해주세요.
          </Question>
          <FacilitiesWrapper>
            {facilities.map((amenity) => (
              <AmenityButton
                key={amenity.key}
                $isSelected={watchFacilities.includes(amenity.name)}
                onClick={() => handleAmenitySelect(amenity.name)}
              >
                {amenity.name}
              </AmenityButton>
            ))}
          </FacilitiesWrapper>
        </div>
        <SubmitButton type="submit">
          <Icon size={16} name={ICONS.LOCATION_RECOMMAND} />{" "}
          <p>주거지 추천 받기</p>
        </SubmitButton>
      </form>
    </Wrapper>
  );
};

export default Survey;

